import logo from './logo.svg';
import './App.css';
import Render from './Render';
import Pad from './ControlPad';
import DataProvider, { DataHub } from './Model/data';
import { useContext, useState } from 'react';
import TopPad from './ControlPad/topPad';
import 'antd/dist/antd.css';
function App() {
  const [pageOffY, setOffY] = useState(0)
  const [pageOffX, setOffX] = useState(0)
  const { scale } = useContext(DataHub)
  return (

    <div onWheel={e => { e.stopPropagation(); setOffY(i => i - e.deltaY); setOffX(i => i - e.deltaX); console.log(e); }} id='app-container' onKeyDown={e => console.log(e)} className='w-screen h-screen flex overflow-hidden'>
      <Render style={{ transform: `translate(${pageOffX}px,${pageOffY}px)   scale(${scale})` }}></Render>
      <TopPad></TopPad>
      <Pad></Pad>

    </div>

  );
}


export default App;
