import { useContext } from "react"
import { DataHub } from "../Model/data"

const Pad = ()=>{

    const layouts = ['xTree','yTree']
    const {setLayout} = useContext(DataHub)
    return (

        <div className="w-[200px] fixed top-0 right-0 z-50 bg-white h-full   ">

                {layouts.map(layout=>{
                    return (
                       <div className="p-2">
                         <div className="bg-blue-50 rounded-lg cursor-pointer flex justify-center items-center border font-thin text-black py-4 border-blue-400" onClick={()=>setLayout(layout)}>
                            {layout}
                        </div>
                        </div>
                    )
                })}

        </div>

    )
}

export default Pad