import { nanoid } from "nanoid"
import { useContext, useEffect, useLayoutEffect, useState } from "react"
import Card from "../Components/Card"
import { DataHub } from "../Model/data"
import EdgeLine from "./Edge"
import { AiOutlineLoading3Quarters } from 'react-icons/ai'
import { createNode, getEdges, reLayoutEdges, reLayoutTree, structrueObj, structrueObjTab } from "./util"
const Render = ({ style }) => {
    const [load, setLoad] = useState(false)
    const [loadNodes, setLoadNodes] = useState(false)
    const [nodeAdded, setNodeAdded] = useState(false)
    const [toggle, setToggle] = useState(false)
    const [pageOffY, setOff] = useState(0)
    const { nodes, layout, edges, setEdges, setNodes, structrue, setStructrue, theme } = useContext(DataHub)
    window.show = () => {
        console.log(structrue);
    }
    function clickCard({ id }) {
        setNodes(origins => {
            return origins.map(item => {
                return {
                    ...item,
                    selected: item.id === id
                }
            })
        })
    }
    function AddBroNode(e, item, branch) {
        if (e.code === 'Tab') e.preventDefault();
        e.stopPropagation()
        if (!["Enter", 'Tab'].includes(e.code) && e.type === "keydown") return;
        const node = nodes.find(node => node.selected) || item
        if (!node) return;
        if (node.branch === 'root') return;
        const appendNode = createNode()
        appendNode.selected = true
        console.log(e);
        const structrue_ = (e.type === 'keydown' && e.code === 'Tab') ? structrueObjTab(structrue, node.id, appendNode.id, branch) : structrueObj(structrue, node.id, appendNode.id, branch)
        nodes.forEach(node => node.selected = false)
        const nodes_ = [...nodes, appendNode]
        setStructrue(structrue_)
        setNodes(reLayoutTree(structrue_, nodes_, layout))
        setNodeAdded(!nodeAdded)
    }
    function resetNodes() {
        setNodes(origin => {
            return origin.map(node => {
                node.canEdit = false
                node.selected = false
                return node
            })
        })
        setNodes(reLayoutTree(structrue, nodes, layout))
    }
    useEffect(() => {
        const edges_ = getEdges(structrue, nodes)
        setEdges(reLayoutEdges(edges_, nodes))
    }, [nodes])
    useEffect(() => {
        setNodes(reLayoutTree(structrue, nodes, layout))
        setEdges(getEdges(structrue, nodes))
        setLoadNodes(true)

    }, [])
    useEffect(() => {
        setLoad(false)
        setNodes(() => reLayoutTree(structrue, nodes, layout))
        setToggle(!toggle)
    }, [layout])
    useLayoutEffect(() => {
        if (loadNodes) {
            setEdges(reLayoutEdges(edges, nodes))
            setLoad(true)
        }
    }, [loadNodes, toggle])

    return (
        <>
            {!load && <div className="h-full w-full flex justify-center items-center">
                <AiOutlineLoading3Quarters style={{ color: 'black', fontSize: '40px' }}></AiOutlineLoading3Quarters>
            </div>}
            <section style={{ opacity: load ? 1 : 0, width: load ? '100%' : 0 }} onClick={resetNodes} tabIndex={1} id="svg-container" onKeyDown={(e) => { AddBroNode(e) }} className={`relative w-full h-full bg-gray-300   scroll-m-0 z-50   ${theme.bg}`}>

                <div className="" style={{ ...style }} >
                    {
                        load && edges.map(item => {
                            return <EdgeLine item={item}></EdgeLine>
                        })
                    }

                    {
                        nodes.map(item => {
                            return (
                                <Card AddBroNode={AddBroNode} onClick={() => clickCard(item)} key={item.id} item={item}></Card>
                            )
                        })
                    }
                </div>

            </section >
        </>


    )
}
export default Render