import { useContext } from "react"
import { DataHub } from "../Model/data"

function getPath({startNode,endNode,layout}) {
    let control1 = { x: 0, y: 0 }
    let control2 = { x: 0, y: 0 }
    let middle = ((endNode.x - startNode.x)) / 2
    if(layout === 'xTree'){
        control1.y = startNode.y 
        control1.x = startNode.x + middle
        control2.y = endNode.y
        control2.x = control1.x
    }else{
        middle =  ((endNode.y - startNode.y)) / 2
        control1.y = startNode.y + middle
        control1.x = startNode.x 
        control2.y = control1.y
        control2.x = endNode.x
    }
    return `M ${startNode.x} ${startNode.y} C ${control1.x} ${control1.y}, ${control2.x} ${control2.y}, ${endNode.x} ${endNode.y}`
}

const EdgeLine = ({item})=>{
    const {theme,layout} = useContext(DataHub)
    let minX = Math.min(item.position.from.x, item.position.to.x)
                    let minY = Math.min(item.position.from.y, item.position.to.y)
                    let width = Math.abs(item.position.from.x - item.position.to.x)
                    let height = Math.abs(item.position.from.y - item.position.to.y)
                    let offX1 = 0 
                    let offX2 = 0 
                    let offY1 = 0 
                    let offY2 = 0 
                    if(layout === 'xTree'){
                        if(item.position.from.x > item.position.to.x){
                            offX2 = -1 *  (item.position.from.width/2 + 4 )
                            offX1 =   (item.position.to.width/2 + 4  )
                        }
                        else{
                            offX2 = 1 *  (item.position.from.width/2 + 4 )
                            offX1 = -1 *  (item.position.to.width/2 + 4  )
                        }
                    }
                    if(layout === 'yTree'){
                        if(item.position.from.y > item.position.to.y){
                            offY2 = -1 *  (item.position.from.height/2 + 4 )
                            offY1 =   (item.position.to.height/2 + 4  )
                        }
                        else{
                            offY2 = 1 *  (item.position.from.height/2 + 4 )
                            offY1 = -1 *  (item.position.to.height/2 + 4  )
                        }
                    }
                    const  startNode = {
                        x:item.position.from.x - minX - offX1,
                        y:item.position.from.y - minY -  offY1
                    }
                    const endNode = {
                        x:item.position.to.x - minX - offX2,
                        y:item.position.to.y - minY - offY2
                    }
                    return (
                        <svg width={width+100} height={height+100} style={{ left: `${minX - 50}px`, top: `${minY - 50}px` }} className="absolute" key={item.id}>
                            
                               <g transform="  translate(50,50)  ">
                               <path
                                stroke={theme.edge || '#000'}
                                fill="transparent"
                                strokeWidth={3}
                                d={getPath({ startNode,  endNode ,layout })}
                                 />
                                 <circle cx={startNode.x} cy={startNode.y} r={2} fill="blue" />
                                 <circle cx={endNode.x} cy={endNode.y} r={2} fill="blue" />
                               </g>
                        </svg>
                    )
}

export default EdgeLine