import { nanoid } from "nanoid";
import { createContext, Fragment, useState } from "react";

const themeData = {
    theme: {},
    setTheme: ''
}

const fontSizeData = {
    fontSize: '19px',
    setFontSize: ''
}

const layoutData = {
    layout: 'xTree',
    setLayout: ''
}

export const DataHub = createContext({
    nodes: [],
    setNodes: '',
    structrue: {},
    setStructrue: '',
    edges: [],
    setEdges: '',
    scale: '',
    setScale: '',
    ...themeData,
    ...layoutData,
    ...fontSizeData
})

const DataProvider = ({ children }) => {
    const [nodes, setNodes] = useState([{
        title: 'ROOT',
        position: {
            x: 500,
            y: 300
        },
        width: 200,
        height: -1,

        id: nanoid()
    }, {
        title: 'title',
        position: {
            x: 0,
            y: 0
        },
        width: 200,
        height: -1,
        id: nanoid()
    },
    {
        title: 'title',
        position: {
            x: 0,
            y: 0
        },
        width: 200,
        height: -1,
        id: nanoid()
    }, {
        title: 'title',
        position: {
            x: 0,
            y: 0
        },
        width: 200,
        height: -1,
        id: nanoid()
    }, {
        title: 'title',
        position: {
            x: 0,
            y: 0
        },
        width: 200,
        height: -1,
        id: nanoid()
    }, {
        title: 'title',
        position: {
            x: 0,
            y: 0
        },
        width: 200,
        height: -1,
        id: nanoid()
    }])
    const [edges, setEdges] = useState([])
    const [structrue, setStructrue] = useState({
        id: nodes[0].id,
        leftChildren: [
            { id: nodes[1].id, leftChildren: [], },
            { id: nodes[2].id, leftChildren: [], },
            { id: nodes[3].id, leftChildren: [], }
        ],
        rightChildren: [
            { id: nodes[4].id, rightChildren: [] },
            { id: nodes[5].id, rightChildren: [] },
        ]
    })
    const [theme, setTheme] = useState({
        card: 'bg-blue-200 text-blue-800',
        bg: 'bg-blue-100',
        edge: '#0084ff',
        name: 'blue'
    })
    const [scale, setScale] = useState(1)
    const [fontSize, setFontSize] = useState('19px')
    const [layout, setLayout] = useState('xTree')
    const value = {
        nodes, setNodes, edges, setEdges, structrue, setStructrue, theme, setTheme, scale, setScale, layout, setLayout, fontSize, setFontSize
    }
    return (
        <DataHub.Provider value={{ ...value }}>
            {children}
        </DataHub.Provider>
    )
}
export default DataProvider