import { useContext } from "react"
import { DataHub } from "../../Model/data"
import { roundClassName } from "../../Render/theme/round"
import {IoAddCircle} from 'react-icons/io5'
const Card = ({item,onClick,AddBroNode}) =>{
    const {theme,setNodes,nodes,layout,fontSize} = useContext(DataHub)
    function dbclick() {
        setNodes(origin=>{
            return origin.map(node=>{
                if(node.id === item.id) node.canEdit = true 
                else node.canEdit = false 
                return node
            })
        })
    }
    return (
        <>
            <span onKeyDown={(e)=>{ e.code === 'Enter' && item.canEdit&&e.stopPropagation()}} onClick={(e)=>{onClick();dbclick();e.stopPropagation()}}   id={item.id} style={{top:`${item.position.y}px`,left:`${item.position.x}px `,width:`${item.width}px` }} className={`bg-white border  border-gray-500  p-[20px] rounded-md absolute cursor-pointer block break-words    ${item.selected?'ring-2 ring-blue-400 ':''} ${roundClassName}   hover:ring-1 hover:ring-blue-400  ${theme.card}` }>    
            <span  style={{fontSize:fontSize}} contentEditable={item.canEdit} >
                  {item.title} 
            </span>
            
           { layout === 'xTree' &&['root','right'].includes(item.branch) && <span onMouseDown={(e)=>{;e.stopPropagation();AddBroNode(e,item,'right')}}  className={`w-[10px] flex text-[12px] z-50 justify-center items-center text-white h-[10px] top-1/2 absolute rounded-full right-[-5px]`} style={{background:theme.edge}} >
                  <IoAddCircle style={{color:"#fff"}}></IoAddCircle>
            </span>}
           {layout === 'xTree' && ['root','left'].includes(item.branch) && <span  onMouseDown={(e)=>{;e.stopPropagation();AddBroNode(e,item,'left')}} className={`w-[10px] flex text-[12px] z-50 justify-center items-center text-white h-[10px] top-1/2 absolute rounded-full left-[-5px]`} style={{background:theme.edge}} >
                  <IoAddCircle style={{color:"#fff"}}></IoAddCircle>
            </span>}

            

            {/* ytEe */}
            { layout === 'yTree' && ['root','right'].includes(item.branch) && <span onMouseDown={(e)=>{;e.stopPropagation();AddBroNode(e,item,'right')}}  className={`w-[10px] flex text-[12px] z-50 justify-center items-center text-white h-[10px] top-[-5px] absolute rounded-full right-1/2`} style={{background:theme.edge}} >
                  <IoAddCircle style={{color:"#fff"}}></IoAddCircle>
            </span>}
           { layout === 'yTree' &&['root','left'].includes(item.branch) && <span  onMouseDown={(e)=>{;e.stopPropagation();AddBroNode(e,item,'left')}} className={`w-[10px] flex text-[12px] z-50 justify-center items-center text-white h-[10px] bottom-[-5px] absolute rounded-full right-1/2`} style={{background:theme.edge}} >
                  <IoAddCircle style={{color:"#fff"}}></IoAddCircle>
            </span>}
            
            </span>
        </>
    )
}   

export default Card