import { nanoid } from "nanoid";
import * as d3 from "d3";
export function getEdges(structrue, nodes) {
    const edges = []
    travelLeft(structrue, nodes)
    travelRight(structrue, nodes)
    // console.log(edges, 'geedges');
    return edges
    function travelLeft(structrue) {
        const root = nodes.find(node => node.id === structrue.id)

        if (structrue?.leftChildren?.length) {
            structrue.leftChildren.forEach((child, index) => {
                edges.push({ from: root.id, to: child.id, id: nanoid() })
                travelLeft(child)
            });
        }
    }
    function travelRight(structrue) {
        const root = nodes.find(node => node.id === structrue.id)
        //  console.log(root, structrue);
        if (structrue?.rightChildren?.length) {
            structrue.rightChildren.forEach((child, index) => {
                edges.push({ from: root.id, to: child.id, id: nanoid() })
                travelRight(child)
            });
        }
    }

}


export function reLayoutEdges(edges, nodes) {
    // console.log(edges, 'relay');
    edges.forEach(edge => {
        const { from, to } = edge
        const fromNode = nodes.find(node => node.id === from)
        const toNode = nodes.find(node => node.id === to)
        edge.position = {
            from: { ...fromNode.position, width: fromNode.width, height: fromNode.height },
            to: { ...toNode.position, width: toNode.width, height: toNode.height }
        }
        const fromDom = document.getElementById(from)
        const toDom = document.getElementById(to)
        edge.position.from.x += fromNode.width / 2
        edge.position.to.x += toNode.width / 2
        edge.position.from.y += fromDom.clientHeight / 2
        edge.position.to.y += toDom.clientHeight / 2
    })
    // console.log(edges, 'relay');
    return edges
}
