export function reLayoutTree(structrue, nodes, tlayout) {
    tlayout = tlayout || 'xTree'
    const hier = d3.hierarchy(structrue, (node) => node.leftChildren)
    const rightHier = d3.hierarchy(structrue, (node) => node.rightChildren)

    const layout = d3.cluster().nodeSize(tlayout === 'xTree' ? [100, 220] : [230, 80])
    const res = layout(hier)
    const rightRes = layout(rightHier)
    let delta = {
        x: 0,
        y: 0
    }
    let rootPosition = {
        x: 0,
        y: 0
    }
    if (tlayout === 'xTree') {
        flip(res, -1 * Math.PI / 2)

        flip(rightRes, Math.PI / 2)

    } else if (tlayout === 'yTree') {
        flip(res, 0)

        flip(rightRes, Math.PI)
    }
    travel(res);
    travel(rightRes, 1)

    function flip(root, rotate) {
        let dd = {
            x: root.x,
            y: root.y
        }
        root.x = 0
        root.y = 0
        travel(root)
        function travel(node) {

            if (node.children) {
                node.children.forEach(item => {
                    item.x -= dd.x
                    item.y -= dd.y
                    let rad = Math.atan2(-1 * item.y, item.x)
                    let Distance = Math.ceil(Math.sqrt(item.y * item.y + item.x * item.x))
                    item.x = Distance * Math.cos(rad + rotate)
                    item.y = -1 * Distance * Math.sin(rad + rotate)

                    travel(item)
                })
            }
        }
    }

    function travel(iNode, bool) {
        const root = nodes.find(node => node.id === iNode.data.id)
        if (iNode.depth === 0) {
            delta.x = iNode.x - root.position.x
            delta.y = iNode.y - root.position.y
        } else {
            root.position.x = iNode.x - delta.x
            root.position.y = iNode.y - delta.y
        }

        root.branch = bool ? 'right' : 'left'
        if (iNode.depth === 0) root.branch = 'root'
        if (iNode.children) {
            iNode.children.forEach(child => {

                travel(child, bool)
            })
        }
    }
    return nodes


}

export function structrueObj(structrue, id, apNodeId, branch) {
    if (structrue.id === id) {
        branch === 'left' && structrue.leftChildren.push({ id: apNodeId, leftChildren: [] });
        branch === 'right' && structrue.rightChildren.push({ id: apNodeId, rightChildren: [] });
        return structrue;
    }

    findLeftTree(structrue)
    findRightTree(structrue)

    let res = structrue
    // console.log(structrue.id, 'idx');
    return res
    function findLeftTree(structrue) {
        structrue?.leftChildren?.forEach(item => {
            if (item.id === id) {
                console.log(item, '__x');
                item.leftChildren.push({ id: apNodeId, leftChildren: [] });
                return structrue
            } else {
                findLeftTree(item)
            }
        })

    }
    function findRightTree(structrue) {
        structrue?.rightChildren?.forEach(item => {
            if (item.id === id) {
                item.rightChildren.push({ id: apNodeId, rightChildren: [] });
                return structrue
            } else {
                findRightTree(item)
            }
        })

    }
}

